/* Main container styling */
.main-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100vh;
  background-color: #fff0f5; /* Soft pinkish background for love theme */
  color: #333;
  padding: 20px;
}

/* Header styling with love theme */
.header {
  text-align: center;
  margin-bottom: 20px;
}

.title {
  font-size: 42px;
  font-family: 'Arial', sans-serif;
  color: #d32f2f; /* Soft red for the love theme */
  margin: 0;
  letter-spacing: 2px;
}

/* Button container */
.menu-buttons {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 300px;
  margin-top: 20px;
}

/* Menu button styling */
.menu-button {
  background-color: #ffeb3b;
  color: #333;
  border: none;
  padding: 15px;
  margin: 10px 0;
  font-size: 22px;
  cursor: pointer;
  border-radius: 30px; /* Rounded for a softer look */
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.menu-button:hover {
  background-color: #ffd600;
  transform: scale(1.05); /* Slight zoom on hover */
}

.menu-button:disabled {
  background-color: #ddd;
  color: #999;
  cursor: not-allowed;
}

/* High score section */
.high-score {
  margin-top: 20px;
}

.high-score h2 {
  font-size: 22px;
  font-family: 'Arial', sans-serif;
  color: #333;
  margin: 0;
}

/* Footer styling with love */
.footer {
  margin-top: auto;
  padding: 10px;
  text-align: center;
  font-size: 16px;
  color: #d32f2f; /* Match the love theme */
}

/* Responsive design for smaller screens */
@media (max-width: 768px) {
  .title {
    font-size: 36px;
  }

  .menu-button {
    font-size: 20px;
    padding: 12px;
  }

  .high-score h2 {
    font-size: 20px;
  }

  .footer {
    font-size: 14px;
  }
  .now-playing {
    font-size: 16px;
    color: #d32f2f;
    margin-top: 20px;
    font-weight: bold;
  }
  
}
