.end-game-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    max-width: 500px;
    margin: 0 auto;
  }
  
  .word-list {
    list-style-type: none;
    padding: 0;
    margin: 20px 0;
    max-height: 300px;
    overflow-y: auto;
    width: 100%;
    text-align: left;
  }
  
  .word-list li {
    font-size: 1.2rem;
    padding: 10px 15px;
    border-bottom: 1px solid #e0e0e0;
  }
  
  .found-word {
    color: green;
    background-color: #eaffea;
  }
  
  .missed-word {
    color: red;
    background-color: #ffe6e6;
  }
  
  button {
    background-color: #4caf50;
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    font-size: 1rem;
    border-radius: 5px;
    margin-top: 20px;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #45a049;
  }
  