/* Basic styling for the game container */
.container {
  text-align: center;
  margin-top: 70px; /* Ensure space for TopBar */
  padding: 0 10px;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Letter grid with soft rounded look */
.letter-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 12px;
  margin-bottom: 20px;
}

.letter {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #ffb3b3; /* Soft pink */
  font-size: 24px;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.letter.central {
  background-color: #d32f2f; /* Central letter in deep red */
  color: white;
}

.letter:hover {
  transform: scale(1.1);
}

/* Input field for word submission */
input[type="text"] {
  padding: 12px;
  font-size: 18px;
  width: 100%;
  max-width: 300px;
  margin-bottom: 20px;
  border-radius: 25px;
  border: 1px solid #ccc;
  text-align: center;
  background-color: #fff7f9;
  color: #333;
}

input[type="text"]:focus {
  border-color: #d32f2f;
}

/* Disable keyboard popup */
input[readonly] {
  cursor: pointer;
}

/* Found words container */
.found-words-container {
  margin-top: 10px;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 15px;
  background-color: #fff7f9;
  text-align: left;
  margin-bottom: 20px;
  height: 150px;
  width: 100%;
  max-width: 600px;
  overflow-y: auto;
}

.found-words-container h2 {
  margin-bottom: 10px;
  font-size: 20px;
  color: #d32f2f;
}

/* Word list styling */
.word-list {
  list-style-type: none;
  padding-left: 0;
  font-size: 16px;
}

.word-list li {
  padding: 5px 0;
  border-bottom: 1px solid #ddd;
  color: #555;
}

/* Score styling */
.score {
  font-size: 24px;
  color: #333;
  margin-top: 10px;
}

/* Controls with rounded buttons */
.controls {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.controls button {
  background-color: #ff5252; /* Soft red */
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.controls button:hover {
  background-color: #ff8a80;
  transform: scale(1.05);
}

/* End game button with soft rounded look */
.end-game-btn {
  padding: 12px 16px;
  font-size: 18px;
  background-color: #d32f2f;
  color: white;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  width: 150px;
  transition: background-color 0.3s ease;
}

.end-game-btn:hover {
  background-color: #ff5252;
}

/* Responsive design for mobile */
@media (max-width: 768px) {
  .letter {
    width: 50px;
    height: 50px;
    font-size: 20px;
  }

  input[type="text"] {
    width: 80%;
  }

  .controls button {
    font-size: 16px;
    padding: 8px 16px;
  }

  .end-game-btn {
    font-size: 14px;
    padding: 8px 12px;
    width: 100%;
  }

  .found-words-container {
    max-width: 100%;
  }
}
