/* TopBar Styling with love theme */
.top-bar {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    height: 60px;
    background-color: #d32f2f; /* Soft red background */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000; /* Stay on top */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow for a professional feel */
  }
  
  .top-bar-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1000px;
    padding: 0 20px;
  }
  
  .audio-controls {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  /* Title with love theme */
  .top-bar-title {
    color: white;
    font-size: 24px;
    font-family: 'Arial', sans-serif;
    font-weight: bold;
    text-align: center;
    margin: 0;
    letter-spacing: 1px;
  }
  
  .exit-button {
    color: white;
    cursor: pointer;
  }
  
  /* Responsive design */
  @media (max-width: 768px) {
    .top-bar-title {
      font-size: 20px;
    }

    .audio-controls {
        display: flex;
        align-items: center;
        gap: 10px;
      }
      
      .volume-slider {
        width: 100px;
        background: transparent;
        cursor: pointer;
      }
      
  }
  